import React from "react";
import "../ComponentStyle/HomeStyle.css";
import ArrowLeft from "../Asset/ArrowLeft.svg";
import ArrowRight from "../Asset/ArrowRight.svg";
import music from "../Asset/music.svg";
import sfx from "../Asset/sfx.svg";

import home from "../Asset/homeIcon.svg";
import menu from "../Asset/menu.svg";

const About =()=> {
    return (
        <div className="ScreenContainer">
            <div className="Profil" >
                <h1>Petunjuk</h1>
                <div className="petunjuk">
                    <img src={ArrowLeft}  />
                    <p>Navigasi kembali</p>
                </div>
                <div className="petunjuk">
                    <img src={ArrowRight}  />
                    <p>Navigasi berikutnya</p>
                </div>
                <div className="petunjuk">
                    <img src={music}  />
                    <p>Tombol backsound</p>
                </div>
                <div className="petunjuk">
                    <img src={sfx}  />
                    <p>Tombol sound effect</p>
                </div>
                <div className="petunjuk">
                    <img src={home}  />
                    <p>Tombol navigasi kembali ke home</p>
                </div>
                <div className="petunjuk">
                    <img src={menu}  />
                    <p>Tombol navigasi membuka menu</p>
                </div>
            </div>
        </div>
    )
}

export default About