import React from "react";
import "../ComponentStyle/ModalStyle.css";
import Xicon from "../Asset/XIcon.svg";
import { Link } from "react-router-dom";
import IconVerb from "../Asset/IconVerb.png";
import IconAdverb from "../Asset/IconAdverb.png";
import QuizzVerb from "../Asset/QuizzVerb.png";
import QuizzAdverb from "../Asset/QuizzAdverb.png";
import VideoVerb from "../Asset/VideoVerb.png";
import VideoAdverb from "../Asset/VideoAdverb.png";


const MenuModal =({open,onClose})=> {
    if(!open) return null
    return(
        <div className="Overlay">
             <div className="XContainer">
                <img onClick={onClose} src={Xicon} />
            </div>
            <div className="ButtonContainer">
                <Link to="/Screen3" className="BackLink Link">
                    <button onClick={onClose}><h1>Materi Verb</h1><img className="icons" src={IconVerb} /></button>
                </Link> 
                <Link to="/Video" className="BackLink Link">
                    <button onClick={onClose}><h1>Video Verb</h1><img className="icons" src={VideoVerb} /></button>
                </Link> 
                <Link to="/Quizz" className="BackLink Link">
                    <button onClick={onClose}><h1>Quizz Verb</h1><img className="icons" src={QuizzVerb} /></button>
                </Link> 
                <Link to="/Screen6" className="BackLink Link">
                    <button onClick={onClose}><h1>Materi Adverb</h1><img className="icons" src={IconAdverb} /></button>
                </Link> 
                <Link to="/Video1" className="BackLink Link">
                    <button onClick={onClose}><h1>Video Verb</h1><img className="icons" src={VideoAdverb} /></button>
                </Link> 
                <Link to="/Quizz1" className="BackLink Link">
                    <button onClick={onClose}><h1>Quizz Adverb</h1><img className="icons" src={QuizzAdverb} /></button>
                </Link> 
            </div>
        </div>        
    )
}

export default MenuModal